import React from "react";
import axios from 'axios';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";


class Contact extends React.Component {
    state = { details: [] };
    server = 'http://localhost:8000';

    componentDidMount() {
        axios.get(`${this.server}/prod/`)
        .then(res => {
            this.setState({
                details: res.data
            });
        })
        .catch(err => console.error(err));
    }

    render() {
        const sliderSettings = {
          dots: true,
          infinite: true,
          speed: 500,
          slidesToShow: 1,  // Exibe um slide por vez
          slidesToScroll: 1,
        };

        return (
          <div id="produtos" className="product">
            {/* elementos */}
            
            {this.state.details.length > 0 ? (
              <Slider {...sliderSettings}>
                {this.state.details.map((data, id) => (
                  <div key={id} className="product-card">
                    
                    <div className="product-text">
                      <div className="nome-product">{data.nome}</div>
                      <div className="descricao-product">Descrio: {data.descricao}</div>
                      <div className="preco-product">Preo: {data.preco}</div>
                    </div>
                    
                      <div className="div-img-product"><img className="img-product" src={data.imagem} alt="" /></div>
                    
                  </div>
                ))}
              </Slider>
            ) : (
              <p>Carregando produtos...</p>
            )}
                    <div className="element-contact" id="element-contact">
                      <img className="imgsorvete3" src="/img/sorvete.svg" alt="Sorvete 3" />
                      <img className="imgsorvete4" src="/img/sorvete.svg" alt="Sorvete 3" />
                      <img className="imgacai6" src="./img/acai.svg" alt="Aa 6" />
                      <img className="imgacai7" src="/img/acai.svg" alt="Aa 7" />
                      <img className="imgacai8" src="/img/acai.svg" alt="Aa 8" />
                      <img className="imgacai9" src="/img/acai.svg" alt="Aa 9" />
                      <img className="imgacai10" src="/img/acai.svg" alt="Aa 10" />
                    </div>
          </div>
        );
      }
}

export default Contact;
